import genWords from 'random-words'

const images = [
  'http://joxi.ru/Y2L4WNBTnyvM62.jpg',
  'http://joxi.ru/8AnWwgOIqYKNXr.jpg',
  'http://joxi.ru/zAN4LqGTlQw1Lm.jpg',
  'http://joxi.ru/Vrwnw5NcKpgo1A.jpg',
  'http://joxi.ru/DmB4G3qTN966dA.jpg',
  'http://joxi.ru/82343DET6owwXA.jpg',
  'http://joxi.ru/KAxXw6NT4dwwLA.jpg',
  'http://joxi.ru/p271xDvc0MZZYr.jpg',
  'http://joxi.ru/KAxXw6NT4dwqLA.jpg',
  'http://joxi.ru/brRjR0GiQ58VDr.jpg',
  'http://joxi.ru/eAOMKgGc40GE8m.jpg',
]

const width = [1, 2, 4]

const genBlock = (el, i) => ({
  id: Date.now() + i,
  title: genWords({ exactly: 4, join: ' ' }),
  viewed: i,
  shared: i+1,
  image: images[Math.floor(Math.random() * images.length)],
  width: width[Math.floor(Math.random() * width.length)],
  createdAt: new Date(),
  height: width[Math.floor(Math.random() * width.length)],
})

const genBlocks = limit => [...Array(limit)].map(genBlock)

const initialState = {
  blocks: genBlocks(20),
}


export default (state = initialState, action = {}) => {
  switch (action.type) {
    case 'BLOCKS LOADED':
      return {
        ...state,
        blocks: action.payload.blocks,
      }
    case 'LOAD MORE BRICKS':
      return {
        ...state,
        blocks: [...state.blocks, ...genBlocks(20)],
      }
    default:
      return state
  }
}



