const initialState = {
  count: 0,
}


export default (state = initialState, action = {}) => {
  switch (action.type) {
    case 'INCREMENT':
      return {
        ...state,
        count: state.count + 1,
      }
    case 'INCREMENT2':
      return {
        ...state,
        count: state.count + 10,
      }
    case 'FETCH2':
      //console.log('FETCH2', action)
      return {
        ...state,
        count: state.count + 5,
      }
    case 'RESET':
      return {
        ...state,
        count: 0,
      }
    default:
      return state
    }
  }



