const initialState = {
  user: null,
  error: null,
  isLoggedIn: false,
}


export default (state = initialState, {type, user, error}) => {
  switch (type) {
    case 'USER_LOGIN_SUCCESS':
      return {
        ...state,
        user,
        error: null,
        isLoggedIn: true,
      }
    case 'USER_LOGIN_FAIL':
      return {
        ...state,
        error,
      }
    case 'USER_LOGGED_OUT':
      return {
        ...initialState,
      }
    default:
      return state
  }
}



