export default (state=initialState, action={}) => {
  //console.log('News reducer', action, state)

  switch (action.type) {
    case 'ALL_NEWS_FETCH_OK':
      return {
        ...state,
        data: action.payload.data,
        error: null,
      }
    case 'ALL_NEWS_FETCH_ERROR':
      return {
        ...state,
        data: null,
        error: action.payload.data,
      }
    default:
      return state
  }
}



