import axios from 'axios'
import React from 'react'

const basePath = '' //process.env.REACT_STATIC_ENV === 'production' ? '/Jump-TV' : ''

export default {

  //csiteRoot: 'https://stogniev.github.io/dist',
  basePath: basePath,
  firebase: {
    apiKey: 'AIzaSyCfLjmvUPI2Y4-8YTnwAx6Jg9KCUUgW2D4',
    authDomain: 'jump-tv.firebaseapp.com',
    databaseURL: 'https://jump-tv.firebaseio.com',
    projectId: 'jump-tv',
    storageBucket: 'jump-tv.appspot.com',
    messagingSenderId: '616656212948',
  },
  renderToHtml: (render, Comp, meta) => {
    const html = render(<Comp />)
    //const styles = flush()
    //meta.styleTags = styles
    return html
  },
  Document: class CustomHtml extends React.Component {
    render () {
      const { Html, Head, Body, children, renderMeta } = this.props

      return (
        <Html>
          <Head>
            <meta charSet="UTF-8" />
            <meta name="viewport" content="width=device-width, initial-scale=1" />

            {/* TEMP */}
            <meta charSet="UTF-8" />
            <meta httpEquiv="X-UA-Compatible" content="IE=edge" />
            <meta name="viewport" content="width=device-width, initial-scale=1" />
            <link rel="icon" href={`${basePath}/assets/img/favicon.ico`} type="image/png" />
            <title>Dashboard</title>
            <link rel="stylesheet" type="text/css" href={`${basePath}/assets/css/bootstrap.min.css`} />
            <link rel="stylesheet" type="text/css" href={`${basePath}/assets/css/font-awesome.min.css`} />
            <link rel="stylesheet" type="text/css" href={`${basePath}/assets/css/components/component.css`} media="all" />
            <link rel="stylesheet" type="text/css" href={`${basePath}/style.css`} media="all" />

            {renderMeta.styleTags}
          </Head>
          <Body>{children}</Body>
        </Html>
      )
    }
  },

  getSiteData: () => ({
    title: 'React Static',
  }),

  getRoutes: async () => {
    const { data: posts } = await axios.get('https://jsonplaceholder.typicode.com/posts')
    return [
      {
        path: '/',
        component: 'src/containers/Home',
      },
      {
        path: '/dashboard',
        component: 'src/containers/Dashboard',
      },
      {
        path: '/react-motion-drawer-demo',
        component: 'src/containers/ReactMotionDrawerDemo',
      },
      {
        path: '/about',
        component: 'src/containers/About',
      },
      {
        path: '/blog',
        component: 'src/containers/Blog',
        getData: () => ({
          posts,
        }),
        children: posts.map(post => ({
          path: `/post/${post.id}`,
          component: 'src/containers/Post',
          getData: () => ({
            post,
          }),
        })),
      },
      {
        path: '/bricks',
        component: 'src/containers/Bricks',
      },
      {
        path: '/navigationIcons',
        component: 'src/containers/navigationIcons',
      },
      {
        path: '/drop-zone-demo',
        component: 'src/containers/DropZoneDemo',
      },
      {
        path: '/react-tube-embed-demo',
        component: 'src/containers/ReactTubeEmbedDemo',
      },
      {
        path: '/nuzzel-api-demo',
        component: 'src/containers/Nuzzel',
      },
      {
        path: '/login-demo',
        component: 'src/containers/LoginDemo',
      },
      {
        path: '/news',
        component: 'src/containers/News',
      },
      {
        path: '/login',
        component: 'src/containers/SignIn',
      },
      {
        path: '/signup',
        component: 'src/containers/SignUp',
      },
      {
        path: '/profile',
        component: 'src/containers/Profile',
      },
      {
        is404: true,
        component: 'src/containers/404',
      },
    ]
  },
  webpack: (config, { defaultLoaders }) => {
    config.module.rules = [
      {
        oneOf: [
          {
            test: /\.json$/,
            use: [{ loader: 'json-loader' }],
          },
          {
            test: /\.(?:png|jpg|svg)$/,
            use: [{ loader: 'file-loader' }],
          },
          defaultLoaders.jsLoader,
          defaultLoaders.cssLoader,
          defaultLoaders.fileLoader,
        ],
      },
    ]
    return config
  },

}



