function* fetchSomething() {
  yield takeEvery('FETCH', fetchSomethingSaga)
}

function* fetchNews(action) {
  const news = yield call(nuzzel.searchNuzzelNews, action.payload)
  yield put({type: 'NEWS FETCHED', news})
}

function* searchNews() {
  yield takeLatest('SEARCH NEWS', fetchNews)
}


function* fetchAllNews(action) {
  try {
    console.log('fetchAllNews', action.payload)
    const r = yield call(functions.httpsCallable('searchAllNews'), action.payload)
    yield put({type: 'ALL_NEWS_FETCH_OK', payload: r})
  } catch(error) {
    yield put({type: 'ALL_NEWS_FETCH_ERROR', payload: error})
  }
}


function* searchAllNews() {
  yield takeLatest('SEARCH_ALL_NEWS', fetchAllNews)
}


function* logInUserWithEmailAndPass({ payload: { email, password } }) {
  try {
    console.log('USER LOGIN DATA  - ', email, password);
    const user = yield call(auth.signInWithEmailAndPassword.bind(auth), email, password);

    window.location = `${config.basePath}/news`;

    yield put({type: 'USER_LOGIN_SUCCESS', user})
  } catch(error) {
    yield put({type: 'USER_LOGIN_FAIL', error})
      alert('Ooops! User login failed');
  }
}

function* emailPassLogin() {
  yield takeLatest('EMAIL_PASS_LOGIN', logInUserWithEmailAndPass)
}

function* logInUserWithFacebook() {
  try {
    const { user } = yield call(auth.signInWithPopup.bind(auth), providers.facebook)
    yield put({type: 'USER_LOGIN_SUCCESS', user})
  } catch(error) {
    yield put({type: 'USER_LOGIN_FAIL', error})
  }
}

function* facebookLogin() {
  yield takeLatest('FACEBOOK_LOGIN', logInUserWithFacebook)
}

function* logInUserWithTwitter() {
  try {
    const { user } = yield call(auth.signInWithPopup.bind(auth), providers.twitter)
    yield put({type: 'USER_LOGIN_SUCCESS', user})
  } catch(error) {
    yield put({type: 'USER_LOGIN_FAIL', error})
  }
}

function* twitterLogin() {
  yield takeLatest('TWITTER_LOGIN', logInUserWithTwitter)
}

function* signUpUserWithEmailAndPass({payload: {email, password, first, last, phone, emailSubscription} }) {
  try {
    const { user } = yield call(auth.createUserWithEmailAndPassword.bind(auth), email, password)
    const refUser = users.doc(user.uid)

      // NOTE: dummy data for creating user with some information
    const dummyUserData = {
        profile: {
            description: 'I am Iron Man. The suit and I are one. To turn over the Iron Man suit would be to turn over myself, which is tantamount to indentured servitude or prostitution, depending on what state you\'re in. You can\'t have it.',
            title: 'Genius, Billionaire, Playboy, Philanthropist',
            address: {
                state: 'New York',
                country: 'USA',
            },
            hourlyRate: 10000,
            jobs: 2,
            totalEarned: 1234567890,
            totalWorked: 9999,
            followers: 123456,
            following: 7,
        }
    };

    // FIXME: remove object assign, if there is no need in dummy data
    const profile = Object.assign({
        name: { firstName: first, lastName: last },
        phone, email, emailSubscription
    }, dummyUserData);



    yield call(user.updateProfile.bind(user), { displayName: `${first} ${last}`})
    yield call(refUser.set.bind(refUser), profile, { merge: true })
    yield put({type: 'USER_SIGNUP_SUCCESS', user: Object.assign(user, {profile})})

    yield call(logInUserWithEmailAndPass, { payload: { email, password }});
  } catch(error) {
    yield put({type: 'USER_SIGNUP_FAIL', error})
  }
}

function* emailPassSignup() {
  yield takeLatest('EMAIL_PASS_SIGNUP', signUpUserWithEmailAndPass)
}

const onAuthStateChanged = () => console.log(1)||new Promise(resolve => auth.onAuthStateChanged(resolve))

function* getLoggedUser() {
  const { user: { isLoggedIn } } = yield select()
  if (isLoggedIn) return;
  const user = yield call(onAuthStateChanged)
  if (user) {
    const refUser = users.doc(user.uid)
    const profile = yield call(refUser.get.bind(refUser))
    return yield put({type: 'USER_LOGIN_SUCCESS', user: Object.assign(user, { profile: profile.data() })})
  }
  return yield put({type: 'USER_LOGGED_OUT'})
}

function* handleLogOut() {
  yield call(auth.signOut.bind(auth))
  yield put({type: 'USER_LOGGED_OUT'})
}

function* logOut() {
  yield takeLatest('LOG_OUT', handleLogOut)
}

// notice how we now only export the rootSaga
// single entry point to start all Sagas at once
export default function* rootSaga() {
  yield all([
    helloSaga(),
    watchIncrementAsync(),
    fetchSomething(),
    searchNews(),
    searchAllNews(),
    emailPassLogin(),
    facebookLogin(),
    twitterLogin(),
    emailPassSignup(),
    getLoggedUser(),
    logOut(),
  ])
}



